<template>
  <div id="app">
    <div class="main">
      <div class="main-title">
        <h1>vuegress-bar</h1>
        <h2>Lightweight progress-bar component for Vue2.</h2>
      </div>
      <vuegress-bar :sections="sections" :bgColor="bgColor" :fillColor="fillColor" />
      <h3>Custom background and fill color options (click to boxes)</h3>
      <div class="background-selector">
        <div class="color" @click="changeTheme('#7ed6df','#22a6b3')" style="background-color:#7ed6df;">set theme</div>
        <div class="color" @click="changeTheme('#e056fd','#be2edd')" style="background-color:#e056fd;">set theme</div>
        <div class="color" @click="changeTheme('#686de0','#4834d4')" style="background-color:#686de0;">set theme</div>
        <div class="color" @click="changeTheme('#ff7979','#eb4d4b')" style="background-color:#ff7979;">set theme</div>
      </div>
      <div class="separator">
        or try with manual hex codes
      </div>
      <div class="input">
        <div class="title-input">
          <input type="text" v-model="bgColor" placeholder="#22a6b3">
        </div>
        <div class="title-input">
          <input type="text" v-model="fillColor" placeholder="#7ed6df">
        </div>
      </div>
      <h3>Dynamic step count and titles</h3>
      <div class="input">
        <div :key="index" v-for="(step, index) in sections.titles" class="title-input">
          <input type="text" v-model="sections.titles[index]">
        </div>
      </div>
      <div class="actions">
        <div @click="increase()" class="btn-title">increase filled item count</div>
        <div @click="decrease()" class="btn-title">decrease filled item count</div>
        <div @click="addTitle('new title')" class="btn-title">add new step</div>
        <div @click="reset()" class="btn-title">reset steps</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  components: {
  },
  data() {
    return {
      sections: {
        titles: ['summary', 'shipping', 'payment', 'checkout'],
        fillCount: 2
      },
      bgColor: '#E4F5B8',
      fillColor: '#1F01B9'
    }
  },
  methods: {
    changeTheme: function(bg, fill) {
      this.bgColor = bg;
      this.fillColor = fill;
    },
    addTitle: function(title) {
      this.sections.titles.push(title);
    },
    increase: function() {
      if (this.sections.titles.length !== this.sections.fillCount) {
        this.sections.fillCount++;
      }
    },
    decrease: function() {
      if (this.sections.fillCount !== 0) {
        this.sections.fillCount--;
      }
    },
    reset: function() {
      this.sections.titles = ['summary', 'shipping', 'payment', 'checkout'];
    }
  }
}
</script>

